* {
  box-sizing: border-box;
}

body {
  width: 88%;
  max-width: 70em;
  margin: 1em auto;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
}

button {
  color: #000;
  border: 1px solid #000;
  border-radius: 4px;
  justify-content: space-between;
  width: 100%;
  padding: 5px 10px;
  display: flex;
}

button:hover {
  cursor: pointer;
}

label {
  font-weight: bold;
  display: block;
}

fieldset {
  border: none;
}

header {
  color: #20b2aa;
  justify-content: space-around;
  width: 100%;
  margin-bottom: 20px;
  margin-left: 10%;
  font-family: Courier New, Courier, monospace;
  font-size: large;
  display: flex;
}

h2, h3, h4 {
  color: #000;
  text-align: end;
  border-radius: 0;
  margin-left: 50px;
  font-family: Courier New, Courier, monospace;
  font-style: italic;
}

@media (width <= 600px) {
  body {
    width: 100%;
    margin: .5em auto;
    padding: 1em;
  }

  button, img, input[type="text"], fieldset, input {
    box-sizing: border-box;
    width: 100%;
    margin: 5px;
    padding: 10px;
  }
}

nav a {
  width: 5vw;
}
